import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { TranslateService } from '@ngx-translate/core';
import { UtilsProvider } from '../../providers/utils/utils';
import { HttpProvider } from '../../providers/http/http';
import { CommandsProvider } from '../../providers/commands/commands';

/**
 * Generated class for the DetalleCuentaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-detalle-cuenta',
  templateUrl: 'detalle-cuenta.html',
})
export class DetalleCuentaPage {

  public position: any;
  public valor : any;
  public valor2 : boolean=true;
  public lista1: string[];
  public lista2: any;
  public lista3: any;
  public lista4: any;
  public saving: string;
  public balance: any;
  public number: any;
  public cange: any;
  public total: any;
  public detalleCuenta : string;
  split: any;

  //variables para los nombre de los textos de los atributos
  public atributo_1 : string;
  public atributo_2 : string;
  public atributo_3 : string;
  public atributo_4 : string;
  public atributo_5 : string;

  public message: string;
  public cuentaDetalle: string[];
  numCuenta: number;
  tipoCuenta: number;
  condicion: string;
  public CA_FECINI:string;
  public CA_FECFIN:string;
  public fecha: Date;

  public listaAccountMvtos: string[] = [];
  public valorDetailAccount : boolean=true;
  public cont : number = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams, private storage: Storage, private translateService: TranslateService, public utils: UtilsProvider,
              public loading: LoadingController, public http: HttpProvider, public command: CommandsProvider) {

  this.condicion = 'none';             
              
  this.valor = this.navParams.get('valor');  

  this.storage.get('noCuenta').then((val) => {  
    this.numCuenta = val;
 });
 this.storage.get('tipoCuenta').then((val) => {  
   this.tipoCuenta = val;
});

  
if(this.valor == 'account'){
    this.lista1  = this.navParams.get('detAccountData');
    var split = (""+this.lista1).split(',');  
    this.saving  = split[4];
    this.balance = split[2];
    this.number  = split[0] + split[3];
    this.cange   = split[1];
    this.total   = split[2];
    this.atributo_1 = this.utils.getMessageLanguage("DetalleCuenta.cuenta");
    this.detalleCuenta =  this.utils.getMessageLanguage("DetalleCuenta.saldoDispo");
    this.atributo_3 = this.utils.getMessageLanguage("DetalleCuenta.numero");
    this.atributo_4 = this.utils.getMessageLanguage("DetalleCuenta.saldocange");
    this.atributo_5 = this.utils.getMessageLanguage("DetalleCuenta.saldoctotal");
  
 }
  else if(this.valor == 'credit'){
          this.lista2  = this.navParams.get('creditData');
          var split = (""+this.lista2).split(',');  
          this.saving  = split[0];
          this.balance = split[3];
          this.number  = split[0];
          this.cange   = split[4];
          this.total   = split[1];
          this.atributo_1 = this.utils.getMessageLanguage("DetalleCuenta.cuenta");
          this.detalleCuenta =  this.utils.getMessageLanguage("DetalleCuenta.saldoDispo");
          this.atributo_3 = this.utils.getMessageLanguage("DetalleCuenta.numero");
          this.atributo_4 = this.utils.getMessageLanguage("DetalleCuenta.saldocange");
          this.atributo_5 = this.utils.getMessageLanguage("DetalleCuenta.saldoctotal");
  }
  else if(this.valor == 'loans'){
          this.valor2 = false;
          this.valorDetailAccount = false;
          this.lista3  = this.navParams.get('loanData');
          var split = (""+this.lista3).split(',');  
          this.saving  = split[1] +" "+ split[3];
          this.balance = split[4]+"%";
          var year = split[5].substr(0,4);
          var mouth= split[5].substr(4,2);
          var day= split[5].substr(6,2);
          this.number  = mouth +"/"+ day+"/"+ year;
          this.cange   = split[2];
          this.total   = split[6];
          this.atributo_1 = this.utils.getMessageLanguage("DetalleCuenta.CreditNumber");
          this.detalleCuenta =  this.utils.getMessageLanguage("DetalleCuenta.saldoDispo");
          this.atributo_3 = this.utils.getMessageLanguage("DetalleCuenta.dueDate");
          this.atributo_4 = this.utils.getMessageLanguage("DetalleCuenta.loanAmount");
          this.atributo_5 = this.utils.getMessageLanguage("DetalleCuenta.balance");
  }
  else if(this.valor == 'investment'){
          this.lista4  = this.navParams.get('investmentData');
          var split = (""+this.lista4).split(',');  
          this.saving  = split[0];
          this.balance = split[2];
          this.number  = split[1] + split[3];
          this.cange   = split[5];
          this.total   = split[6];
          this.atributo_1 = this.utils.getMessageLanguage("DetalleCuenta.cuenta");
          this.detalleCuenta =  this.utils.getMessageLanguage("DetalleCuenta.saldoDispo");
          this.atributo_3 = this.utils.getMessageLanguage("DetalleCuenta.numero");
          this.atributo_4 = this.utils.getMessageLanguage("DetalleCuenta.saldocange");
          this.atributo_5 = this.utils.getMessageLanguage("DetalleCuenta.saldoctotal");
  }
  
  }

  ionViewDidLoad() {
    
  }

  ionViewDidEnter() {
   
    if(this.valor == 'account'){ 
      this.loadMovement();
    }
    
    /*if(this.valor != 'loans'){
      //this.detailCuenta();
    }*/
  }

//nuevo
  public detailCuenta() {   
    
    //var command = 'E2LSCR210';
    var command :string[] =  this.utils.getCommands(this.command.E2LSCR210);

    var param = command[0] + '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID=' + this.utils.getSessionId() + '&E2EW=' + this.utils.separator + 'Enviar' + this.utils.separator + this.utils.separator + 'E2SECR61P' + this.utils.separator + '0' + this.utils.separator + this.utils.separator + '&PAR=' + this.numCuenta + '|' + this.tipoCuenta + '+%BA20+%BA+%BA01282014+%BA12282017+%BA+%BA+%BA+%BA+%BA+%BA+%BA+%BA+%BA+%BA';

    this.http.callServer(param).then(res => {
      this.message = res.message;

      if (this.message != '' && typeof this.message !== "undefined") {
        this.utils.alertServidor(this.message);
      } else {
        this.cuentaDetalle = res.lista1;
        this.cuentaDetalle.splice(0,1);
        //this.storage.set("lista1", this.lista1);
      }

    },
      error => {
        console.log(error);
      });
  }

  //Metodo Cargue  de movimientos. (productOverView -> account -> movimientos)
  private loadMovement() { 
 
    var command :string[] =  this.utils.getCommands(this.command.E2LSCR210);
    var account = this.numCuenta + "|"+  this.tipoCuenta;
    //var key = this.CA_KEYWORD;
    var key = "";

      // mes dia año
      //this.CA_FECINI='03242010';
      //this.CA_FECFIN='04052018';

      //trae la fecha actual
      var date = new Date();
      this.fecha = date; 

      //Fecha actual
      var diaACTUAL = (parseInt(this.fecha.getDate().toString()) <= 9 ) ? "0"+this.fecha.getDate().toString() : this.fecha.getDate().toString();
      var mesACTUAL = ( (this.fecha.getMonth()+1)  <= 9 ) ? "0"+(this.fecha.getMonth()+1).toString() : (this.fecha.getMonth()+1).toString() ;
      var añoACTUAL = this.fecha.getFullYear().toString();

      //Fecha restado los 2 meses
      var diaFIN = ( parseInt(this.fecha.getDate().toString()) <= 9 ) ? "0"+this.fecha.getDate().toString() : this.fecha.getDate().toString();
      var mesFIN = ( (this.fecha.getMonth()+1)  <= 9 ) ? "0"+((this.fecha.getMonth()+1)-2).toString() : ((this.fecha.getMonth()+1)-2).toString() ;
      var añoFIN = this.fecha.getFullYear().toString();

      this.CA_FECINI = (mesFIN) + (diaFIN) + (añoFIN) ; 
      this.CA_FECFIN = (mesACTUAL) + (diaACTUAL) + (añoACTUAL); 

      var tipoTransaccion: string = "";
      var amountRange1: string = "";
      var amountRange2: string = "";
      var checkREF: string = "";
      var checkREFRange1: string = "";
      var checkREFRange2: string = "";
      var campo7: string = "";
      var campo8: string = "";
      var campo9: string = "";
      var campo10: string = "";
      this.storage.get('contador').then((val) => {  
        if(val != null){
          this.cont = val;
        }        
      });
      

      var URL=  command[0]+ '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID='+this.utils.getSessionId()+ '&E2EW='+ 
      this.utils.separator +'Enviar'+ this.utils.separator +  this.utils.separator + 'E2SECR61P' + this.utils.separator +'0'+this.utils.separator+
      this.utils.separator + '&PAR='+ account + this.utils.separator +this.tipoCuenta+this.utils.separator+ key.toUpperCase() + this.utils.separator +
       this.CA_FECINI+this.utils.separator+ this.CA_FECFIN+ this.utils.separator +
        tipoTransaccion + this.utils.separator + amountRange1 + this.utils.separator + amountRange2 + this.utils.separator + checkREF +
        this.utils.separator + checkREFRange1 + this.utils.separator + checkREFRange2 + this.utils.separator + campo7 +
        this.utils.separator + campo8 + this.utils.separator + campo9 + this.utils.separator + campo10 +this.utils.separator;

      this.http.callServer(URL).then(res => {      
      this.message = res.message;

        if (this.message != '' && typeof this.message!=="undefined") {
          //alert(this.message);
          //this.utils.alertServidor(this.message);
          
          if(this.message == "No data available for your request" && this.cont == 0){
            this.utils.alertServidor(this.message);
            this.storage.set("contador", 1);
          }
          console.log("Respuesta enviada del SERVIDOR: "+this.message);
          //this.utils.presentAlert('Filtro.alert');
        } else {
          this.listaAccountMvtos = res.lista1; 
          var lista = this.listaAccountMvtos.splice(0,1); 
             
        }
      },
        error => {
          console.log(error);
        });
  } 

  logout(){
    this.utils.presentConfirm("LoginPage");
    //this.utils.logout();
  }
  
  movimientos(){
    this.navCtrl.push("FiltroPage", { flagFiltro: 2 });
  }

}



